/**
 * 小程序粘贴
 *
 * @param {string} text 待复制的文本
 * @returns {Promise<void>}
 * @example
 *
 * ```ts
 * clipboardMp('stupid').then(() => {});
 * ```
 */
function clipboardMp(text) {
  return new Promise(function (resolve, reject) {
    if (wx) {
      wx.setClipboardData({
        data: "".concat(text),
        success: function success() {
          resolve();
        },
        fail: function fail(error) {
          console.log(error);
          reject();
        }
      });
    } else {
      reject();
    }
  });
}

export { clipboardMp };
